body {
  font-family: sans-serif;
}

@page  {
  margin: 20mm;
}

html,
body {
  margin: 0;
  padding: 0;
}

main {
  padding: 0 !important;
  margin: 0 !important;
  max-width: 100% !important;
  width: auto !important;
}

header.main {
  padding: 0 0 0.5rem 0;
  margin: 0 0 1rem 0;
  border-bottom: 3px solid #ccc;
  position: initial !important;

  h1 {
    margin-left: 0;
    padding-left: 0;
    transform: none;
    font-size: 4rem;

    &::before {
      content: none;
    }
  }
}

.d-print-none {
  display: none;
}

.sticky {
  position: initial !important;
}

.section {
  > header {
    .title {
      transform: none !important;

      &:before {
        border-bottom: none !important;
      }
    }
  }
  article {
    &.summary.summary-print {
      margin-bottom: 1rem;

      header {
        h1, h2 {
          font-size: 1.15rem;
          font-weight: normal;
          display: inline;
        }

        h1 {
          font-weight: bold;
        }

        h2 {
          &:before {
            content: " â€” "
          }

          strong {
            display: none;
          }
        }
      }
    }
  }
}

.section-main {
  margin-bottom: inherit;
}

.text-muted {
  color: inherit !important;
}
